import React from 'react'
import Chapter from './Chapter'
// import Progress from './Progress'
import styles from './styles.module.css'

interface IProps {
  activeItem: string | null
  handleClickActive: (item: string | null) => void
  data: any
}

const ChapterColumnLeft = ({ handleClickActive, activeItem, data }: IProps) => {
  return (
    <div className={styles.leftColumn}>
      <div className={styles.subColumnPart}>
        <div>
          <div className={styles.titlePart}>{data?.name}</div>
          <div className={styles.descriptionPart}>{data?.description}</div>
          {/* <Progress
            classNameBtn={styles.btnPrimary}
            classNameTitle={styles.timeProgress}
            classNameRemainning={styles.timeRemainning}
            classPercent={styles.percentProgress}
          /> */}
        </div>
      </div>

      {data?.children?.map((item: any) => (
        <Chapter
          key={item?.id}
          handleClickActive={handleClickActive}
          item={item}
          activeItem={activeItem}
        />
      ))}
    </div>
  )
}

export default ChapterColumnLeft
